import React from 'react';
import { Link, graphql } from 'gatsby';
import Layout from '@common/layout';
import SEO from '@utils/SEO';
import Menu from '@common/navbar';
import { Section, Container } from '@styles/global';
import styled from 'styled-components';

const TagList = styled.h4`
    display: flex;
    justify-content: space-between;
`;

function TagsPage(props) {
    const data = props.data.allMdx.group;
    const title = 'All Tags';
    return (
        <Layout>
            <SEO title={title} />
            <Menu />
            <Section>
                <Container>
                    <h2>{title}</h2>
                    <p>
                        Similar to categories, tags are often used to organize
                        your content. A tag is usually only a word or two and
                        reflects the keywords or points of your article.
                    </p>
                    <hr />
                    {data.map(tag => (
                        <div>
                            <Link to={`tag/${tag.fieldValue}`}>
                                <TagList>
                                    {tag.fieldValue}{' '}
                                    <span>{`(${tag.totalCount})`}</span>
                                </TagList>
                            </Link>
                            <hr />
                        </div>
                    ))}
                </Container>
            </Section>
        </Layout>
    );
}

export default TagsPage;

export const tagQuery = graphql`
    query {
        allMdx {
            group(field: frontmatter___tags) {
                fieldValue
                totalCount
            }
        }
    }
`;
